@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100;200;300;400;500;600;700&display=swap);
body {
  padding: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Roboto Mono', monospace;
}

.app{
  display: flex;
  min-height: 100vh;
  width: 100vw;
  /*background-color: #EAF0F9;*/
  background: rgb(252,253,255);
  background: linear-gradient(45deg, rgba(252,253,255,1) 48%, rgb(226, 233, 245) 100%);
}

.inputSectionlayer1{
  /*background-color: #E0EAF9;*/
  background: rgb(236,241,249);
  background: linear-gradient(45deg, rgb(240, 244, 250) 48%, rgba(224,234,249,1) 100%);
  width:35%;
  box-shadow: 4px 0px 0.3em rgba(0, 0, 0, 0.137);
}

.inputSectionlayer2{
  height: 100%;
  /*background-color: #C9D9F2;*/
  background: rgb(221,229,241);
  background: linear-gradient(45deg, rgba(221,229,241,1) 48%, rgba(201,217,242,1) 100%);
  width:90%;
  box-shadow: 4px 0px 0.3em rgba(0, 0, 0, 0.137);
}

.inputSectionlayer3{
  height: 100%;
  background-color: #34487B;
  box-shadow: 4px 0px 0.3em rgba(0, 0, 0, 0.137);
  width:90%;
  padding: 3em;
  position: relative;
}


.secondarySection{
  width: 65%;
  padding: 3em;
}

.verticalLine{
  z-index: 1;
  background-color: #ffc107;
  background-color: white;
  position: absolute;
  left: 5px;
  width: 1px;
  bottom: 0;
  top: 0;
}

.left1{
  left: 6px;
  background-color: #faefcf;
  background-color: #ffc107;
}

.left2{
  left: 12px;
  background-color: #ffdc73;
  background-color: #ffc107;
}

.left3{
  left: 18px;
  background-color: #ffc107;
}

@media only screen and (max-width: 992px) {
  .app {
    flex-direction: column;
  }

  .inputSectionlayer1, .inputSectionlayer2, .inputSectionlayer3{
    width: 100vw;
  }

  
  

  .verticalLine{
    display: none;
  }

  .secondarySection{
    width: 100vw;
  }
}
.tableSectionClass{
    display: flex;
    flex-direction: column;
}
.tableSectionHeader{
    margin-bottom: 2em;
}

.note{
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: 2em;
    font-weight: bold;
    margin-top: 3em;
    /*color: #34487B;*/
}

@media only screen and (max-width: 992px) {
    .tableSectionClass{
        flex-direction: column-reverse;
    }

    .tableSectionHeader{
        order: 3;
    }

    .note{
        margin-top: 1em;
        margin-bottom: 1em;
        font-size: 1.3em;
        font-weight: 600;
    }
  }
td input{
    background-color: transparent;
    border: none;
    width: 100%;
}

td input:focus{
    background-color: transparent;
    border: none;
    outline: none;
}
